<script setup>
import { onMounted, ref, onUnmounted } from 'vue';

const toGoUp = () => scrollTo(0, 0);
const isShow = ref(false);

const handleScroll = () => {
	isShow.value = document.documentElement.scrollTop !== 0;
};

onMounted(() => {
	window.addEventListener('scroll', handleScroll);
});

onUnmounted(() => {
	window.removeEventListener('scroll', handleScroll);
});
</script>

<template>
	<div
		@click="toGoUp"
		id="scroll-button"
		class="scroll-button position-fixed z-3 d-flex justify-content-center align-items-center animate__animated"
		:class="{ animate__pulse: isShow, mostrar: isShow }"
	>
		<i class="bi bi-chevron-bar-up"></i>
	</div>
</template>

<style scoped>
.scroll-button {
	background-color: var(--bs-primary);
	border-radius: 50%;
	bottom: 25px;
	color: var(--bs-white);
	height: 30px;
	opacity: 0;
	position: fixed;
	right: 25px;
	transition: opacity 0.35s ease-out;
	user-select: none;
	width: 30px;
}

#scroll-button:hover {
	cursor: pointer;
	transform: scale(1.1);
}

.button-up {
	width: 15px;
	-webkit-filter: drop-shadow(5px 5px 5px #666666);
	filter: drop-shadow(5px 5px 5px #666666);
}

.mostrar,
.scroll-button .mostrar {
	transition: opacity 0.35s ease-in;
	opacity: 1;
	animation: animate__pulse 0.35s linear;
}

@media only screen and (min-width: 1024px) {
	#scroll-button {
		width: 35px;
		height: 35px;
	}
	.button-up {
		width: 15px;
	}
}
</style>
