<script setup>
import { ref, computed } from 'vue';
import { useI18n } from 'vue-i18n';

const { t, locale } = useI18n();

const languagesAvailables = computed(() => {
	return [
		{
			value: t('languages.es'),
			key: 'es',
			flag: '/images/flags/es.svg',
		},
		{
			value: t('languages.en'),
			key: 'en',
			flag: '/images/flags/en.svg',
		},
		{
			value: t('languages.fr'),
			key: 'fr',
			flag: '/images/flags/fr.svg',
		},
		{
			value: t('languages.it'),
			key: 'it',
			flag: '/images/flags/it.svg',
		},
		{
			value: t('languages.de'),
			key: 'de',
			flag: '/images/flags/de.svg',
		},
		{
			value: t('languages.pt'),
			key: 'pt',
			flag: '/images/flags/pt.svg',
		},
		{
			value: t('languages.nl'),
			key: 'nl',
			flag: '/images/flags/nl.svg',
		},
	];
});

// Default language
const selectedLanguage = ref(locale.value);

// Select language and set locale
const selectLanguage = code => {
	selectedLanguage.value = code;
	locale.value = selectedLanguage.value;
};
</script>

<template>
	<div
		class="selector dropdown rounded-3 bg-body-tertiary position-fixed z-3 start-50 translate-middle-x"
	>
		<button
			class="button btn dropdown-toggle border-0 d-flex justify-content-center align-items-center"
			type="button"
			id="dropdownMenuButton1"
			data-bs-toggle="dropdown"
			aria-expanded="false"
		>
			<img
				class="image me-2"
				:src="`/images/flags/${selectedLanguage}.svg`"
				:alt="
					languagesAvailables.find(option => option.key === selectedLanguage)
						.value + ' flag'
				"
			/>
		</button>
		<ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
			<li v-for="language in languagesAvailables" :key="language.key">
				<a
					class="dropdown-item fs-6"
					href="#"
					@click="selectLanguage(language.key)"
				>
					<img
						class="me-2"
						:src="`/images/flags/${language.key}.svg`"
						:alt="language.value"
						width="25"
					/>
					{{ language.value }}
				</a>
			</li>
		</ul>
	</div>
</template>

<style lang="scss">
.selector {
	bottom: 25px;
	.button {
		.image {
			width: 25px;
		}
	}
}
</style>
